import "bootstrap";
import Swiper, {
    Navigation,
    Pagination
} from "swiper";
import $ from "jquery";
import onlyNumber from "./modules/onlyNumber";
import rapidAnalysis from "./modules/rapidAnalysis";
import modalBind from "./modules/modalBind";
import operatingLever from "./modules/operatingLever";
import unitCost from "./modules/unitCost";
import entrepreneurRisk from "./modules/entrepreneurRisk";
import targetPrice from "./modules/targetPrice";
import marginPrice from "./modules/marginPrice";
import projectProfitPrice from "./modules/projectProfitPrice";
import operatingManagement from "./modules/operatingManagment";

window.addEventListener("DOMContentLoaded", () => {

    "use strict";
    Swiper.use([Navigation, Pagination]);
    const turnoverSwiper = new Swiper(".turnover-slider", {
        loop: true,
        spaceBetween: 20,
        slidesPerView: 4,
        breakpoints: {
            1024: {
                slidesPerView: 3
            },
            768: {
                slidesPerView: 2
            },
            706: {
                slidesPerView: 2
            },
            320: {
                slidesPerView: 1
            },
        },
        //navigaiotn arrows
        navigation: {
            nextEl: '.turnover-button-next',
            prevEl: '.turnover-button-prev',
        }
    });

    const profitabilitySwiper = new Swiper('.profitability-slider', {
        //optional parametres
        loop: true,
        spaceBetween: 20,
        slidesPerView: 4,
        breakpoints: {
            1024: {
                slidesPerView: 3
            },
            768: {
                slidesPerView: 2
            },
            706: {
                slidesPerView: 2
            },
            320: {
                slidesPerView: 1
            },
        },

        //navigaiotn arrows
        navigation: {
            nextEl: ".profitability-button-next",
            prevEl: ".profitability-button-prev",
        },
    });

    const liquiditySwiper = new Swiper(".liquidity-slider", {
        //optional parametres
        loop: true,
        spaceBetween: 20,
        slidesPerView: 4,
        breakpoints: {
            1024: {
                slidesPerView: 3
            },
            768: {
                slidesPerView: 2
            },
            706: {
                slidesPerView: 2
            },
            320: {
                slidesPerView: 1
            },
        },

        //navigaiotn arrows
        navigation: {
            nextEl: '.liquidity-button-next',
            prevEl: '.liquidity-button-prev',
        }
    });

    const capitalSwiper = new Swiper(".capital-slider", {
        //optional parametres
        loop: true,
        spaceBetween: 20,
        slidesPerView: 4,
        breakpoints: {
            1024: {
                slidesPerView: 3
            },
            768: {
                slidesPerView: 2
            },
            706: {
                slidesPerView: 2
            },
            320: {
                slidesPerView: 1
            },
        },

        //navigaiotn arrows
        navigation: {
            nextEl: '.capital-button-next',
            prevEl: '.capital-button-prev',
        }
    });

    onlyNumber(".calc");
    rapidAnalysis();
    operatingLever();
    modalBind();
    unitCost();
    entrepreneurRisk();
    targetPrice();
    marginPrice();
    projectProfitPrice();
    operatingManagement();
});