function entrepreneurRisk() {
    // entrepreneurRisk entrepreneurPrice entrepreneurIncome entrepreneurDirectCost 
    // entrepreneurIndirectCost resProfitabilityAfter resMakeoutAfter resSaleProceeds 
    // resGrossMargin resKoGrossMargin resProfitabilityQual resProfitabilityPrice
    // resMakeoutQual resMakeoutPrice
    const resultEntrepreneur = document.getElementById("resultEntrepreneur");

    const form = document.forms.entrepreneurRisk;
    const resProfitabilityAfter = document.getElementById("resProfitabilityAfter");
    const resMakeoutAfter = document.getElementById("resMakeoutAfter");
    const resSaleProceeds = document.getElementById("resSaleProceeds");
    const resGrossMargin = document.getElementById("resGrossMargin");
    const resKoGrossMargin = document.getElementById("resKoGrossMargin");
    const resProfitabilityQual = document.getElementById("resProfitabilityQual");
    const resProfitabilityPrice = document.getElementById("resProfitabilityPrice");
    const resMakeoutQual = document.getElementById("resMakeoutQual");
    const resMakeoutPrice = document.getElementById("resMakeoutPrice");

    function resEntrepreneurText(profitability, makeout) {
        resultEntrepreneur.insertAdjacentHTML("beforeend", `<div class="alert alert-warning alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
        <p>      ,   <strong>${makeout}</strong>-  ,     .</p> 
        <p>    <strong>${profitability}</strong>-  ,    ,        .</p>
        <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
        </div>`);
    }


    form.addEventListener("submit", (e) => {
        e.preventDefault();

        let saleProceeds = (+form.entrepreneurPrice.value * +form.entrepreneurIncome.value).toFixed(2);
        let grossMargin = ((+form.entrepreneurPrice.value - +form.entrepreneurCost.value) * form.entrepreneurIncome.value).toFixed(2);
        let koGrossMargin = (grossMargin / saleProceeds).toFixed(2);
        let costSum = +form.entrepreneurIndirectCost.value + +form.entrepreneurDirectCost.value;
        let profitabilityQual = Math.round(costSum / (+form.entrepreneurPrice.value - +form.entrepreneurCost.value));
        let profitabilityPrice = (costSum / koGrossMargin).toFixed(2);
        let makeoutQual = Math.round(+form.entrepreneurDirectCost.value / (+form.entrepreneurPrice.value - +form.entrepreneurCost.value));
        let makeoutPrice = (+form.entrepreneurDirectCost.value / koGrossMargin).toFixed(2);

        let ProfitabilityAfter = ((12 * profitabilityQual) / +form.entrepreneurIncome.value).toFixed(2);
        let MakeoutAfter = ((12 * makeoutQual) / +form.entrepreneurIncome.value).toFixed(2);

        resSaleProceeds.textContent = saleProceeds;
        resGrossMargin.textContent = grossMargin;
        resKoGrossMargin.textContent = koGrossMargin;
        resProfitabilityQual.textContent = profitabilityQual;
        resProfitabilityPrice.textContent = profitabilityPrice;
        resMakeoutQual.textContent = makeoutQual;
        resMakeoutPrice.textContent = makeoutPrice;

        resProfitabilityAfter.textContent = ProfitabilityAfter + " .";
        resMakeoutAfter.textContent = MakeoutAfter + " .";

        resEntrepreneurText(profitabilityQual, makeoutQual);
    });

}

export default entrepreneurRisk;