function marginPrice() {
    const marginPricing = document.getElementById("marginPricingUnit");

    const form = document.forms.marginPricing;

    const resMarginRealization = document.getElementById("resMarginRealization");
    const resMarginRealizationUnit = document.getElementById("resMarginRealizationUnit");
    const resMarginPrice = document.getElementById("resMarginPrice");

    function resTargetPriceText(marginPrice) {
        marginPricing.insertAdjacentHTML("beforeend",
            `<div class="alert alert-success alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>       <strong>${marginPrice} ..</strong>:       .</p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
            <div class="alert alert-warning alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>      .</p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
        `);
    }


    form.addEventListener("submit", (e) => {
        e.preventDefault();
        let realizationPrice = +form.marginDirectCost.value + +form.marginProfit.value;
        let realizationPriceUnit = realizationPrice / +form.marginProductionVolume.value;
        let price = +form.marginCost.value + realizationPriceUnit;

        resMarginRealization.textContent = realizationPrice.toFixed(2);
        resMarginRealizationUnit.textContent = realizationPriceUnit.toFixed(2);
        resMarginPrice.textContent = price;

        resTargetPriceText(price);
    });
}

export default marginPrice;