function operatingLever() {
    const form = document.forms.operatingLever;
    const cvpSalesProceeds = document.getElementById("cvpSalesProceeds");
    const cvpVariableCosts = document.getElementById("cvpVariableCosts");
    const cvpFixedCosts = document.getElementById("cvpFixedCosts");
    const cvpChangePercent = document.getElementById("cvpChangePercent");
    const cvpResult = document.getElementById("cvpResult");

    form.addEventListener("submit", (e) => {
        e.preventDefault();

        let resultCVP = ((form.changePercent.value * form.salesProceeds.value - form.changePercent.value * form.variableCosts.value) / (form.changePercent.value * form.salesProceeds.value - form.changePercent.value * form.variableCosts.value - form.changePercent.value * form.fixedCosts.value)).toFixed(2);
        let result = (form.changePercent.value * resultCVP).toFixed(2);
        console.log(resultCVP);
        console.log(result);
        cvpSalesProceeds.textContent = Math.round(form.salesProceeds.value * (1 + (form.changePercent.value * 0.01)));
        cvpVariableCosts.textContent = Math.round(form.variableCosts.value * (1 + (form.changePercent.value * 0.01)));
        cvpFixedCosts.textContent = Math.round(form.fixedCosts.value);
        cvpChangePercent.textContent = form.changePercent.value + "%";
        cvpResult.textContent = result +"%";
    });
}
export default operatingLever;