function projectProfitPrice() {
    const projectProfitability = document.getElementById("projectProfitability");

    const form = document.forms.projectProfitPricing;

    const resProfitCost = document.getElementById("resProfitCost");
    const resProfitUnit = document.getElementById("resProfitUnit");
    const resProfitPrice = document.getElementById("resProfitPrice");

    function resProjectProfitability(profitPrice) {
        projectProfitability.insertAdjacentHTML("beforeend",
            `<div class="alert alert-success alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p> ,    ,      ,      <strong>${profitPrice} ..</strong></p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
            <div class="alert alert-warning alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>               .</p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
        `);
    }


    form.addEventListener("submit", (e) => {
        e.preventDefault();
        let totalCost = (+form.profitCost.value + (+form.profitDirectCost.value / +form.profitProductionVolume.value));
        let needProfit = ((+form.profitCredit.value * (+form.profitCreditValue.value / 100)) / +form.profitProductionVolume.value);
        let price = totalCost + needProfit;

        resProfitCost.textContent = totalCost.toFixed(2);
        resProfitUnit.textContent = needProfit.toFixed(2);
        resProfitPrice.textContent = `  ${price.toFixed(2)} ..`;
        resProjectProfitability(price.toFixed(2));
    });
}

export default projectProfitPrice;