function operatingManagement() {

    const cocResult = document.getElementById("cocResult");

    const formTfp = document.forms.tfp;
    const formCoc = document.forms.coc;

    // TFP elements result
    const resTfpStore = document.getElementById("resTfpStore");
    const resTfpNotesPayable = document.getElementById("resTfpNotesPayable");
    const resTfpBillPayable = document.getElementById("resTfpBillPayable");
    const resTfp = document.getElementById("resTfp");

    //COC elements result
    const resCocSum = document.getElementById("resCocSum");
    const resTfpSum = document.getElementById("resTfpSum");
    const resPotecialCash = document.getElementById("resPotecialCash");
    const resRealCash = document.getElementById("resRealCash");

    formTfp.addEventListener("submit", (e) => {
        e.preventDefault();
        resTfpStore.textContent = formTfp.tfpStore.value;
        resTfpNotesPayable.textContent = formTfp.tfpNotesPayable.value;
        resTfpBillPayable.textContent = formTfp.tfpBillPayable.value;
        let calcTfp = +formTfp.tfpStore.value + +formTfp.tfpNotesPayable.value - +formTfp.tfpBillPayable.value;
        resTfp.textContent = calcTfp;
    });

    formCoc.addEventListener("submit", (e) => {
        e.preventDefault();
        let calcTfp = +formTfp.tfpStore.value + +formTfp.tfpNotesPayable.value - +formTfp.tfpBillPayable.value;
        let calcCoc = ((+formCoc.cocFinancialInstruments.value + +formCoc.cocCash.value + +formCoc.cocOverBackAsset.value) - (+formCoc.cocBorrowingCost.value + +formCoc.cocBillPayable.value + +formCoc.cocOverLiabilities.value));
        let potencialCash = calcCoc - calcTfp;
        let realCash = potencialCash + +formCoc.cocBorrowingCost.value - +formCoc.cocFinancialInstruments.value;

        resCocSum.textContent = calcCoc;
        resTfpSum.textContent = calcTfp;
        resPotecialCash.textContent = potencialCash;
        resRealCash.textContent = realCash;

        if (realCash > 0) {
            cocResult.insertAdjacentHTML("beforeend",
            `<div class="alert alert-success alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p> ()     ,           -   </p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
            `);
        } else if (realCash < 0) {
            cocResult.insertAdjacentHTML("beforeend",
            `<div class="alert alert-danger alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>                ,       </p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
            `);
        }
    });

}

export default operatingManagement;