function unitCost() {
    const formUnitCosts = document.forms.unitCosts;
    //maxProduct minProduct maxCosts minCosts
    const resProduct = document.getElementById("resProduct");
    const resCost = document.getElementById("resCost");
    const resMaxProduct = document.getElementById("resMaxProduct");
    const resMinProduct = document.getElementById("resMinProduct");
    const resMaxCosts = document.getElementById("resMaxCosts");
    const resMinCosts = document.getElementById("resMinCosts");

    formUnitCosts.addEventListener("submit", (e) => {
        e.preventDefault();
        let calcVarProduct = ((((formUnitCosts.maxCosts.value - formUnitCosts.minCosts.value) * (100 / (100 - (formUnitCosts.minProduct.value / formUnitCosts.maxProduct.value * 100)))) / formUnitCosts.maxProduct.value)).toFixed(2);
        let calcVarCosts = (formUnitCosts.maxCosts.value - (calcVarProduct * formUnitCosts.maxProduct.value)).toFixed(2);
        resMaxProduct.textContent = formUnitCosts.maxProduct.value;
        resMinProduct.textContent = formUnitCosts.minProduct.value;
        resMaxCosts.textContent = formUnitCosts.maxCosts.value;
        resMinCosts.textContent = formUnitCosts.minCosts.value;
        resProduct.textContent = calcVarProduct + " ../";
        resCost.textContent = calcVarCosts;
    });
}

export default unitCost;