function modalBind() {
    const btnChevrons = document.querySelectorAll(".btn-chevron");
    const dropdowns = document.querySelectorAll(".dropdown-btn");

    const finLinks = document.querySelectorAll(".fin-link");

    btnChevrons.forEach(chevron => {
        chevron.addEventListener("click", () => {
            if (chevron.lastElementChild.classList.contains("bi-chevron-bar-expand")) {
                chevron.lastElementChild.classList.remove("bi-chevron-bar-expand");
                chevron.lastElementChild.classList.add("bi-chevron-bar-contract");
            } else {
                chevron.lastElementChild.classList.remove("bi-chevron-bar-contract");
                chevron.lastElementChild.classList.add("bi-chevron-bar-expand");
            }
        });
    });

    dropdowns.forEach(dropdown => {
        dropdown.addEventListener("click", () => {

            if (dropdown.lastElementChild.classList.contains("bi-caret-down-fill")) {
                dropdown.lastElementChild.classList.remove("bi-caret-down-fill");
                dropdown.lastElementChild.classList.add("bi-caret-up-fill");
            } else {
                dropdown.lastElementChild.classList.remove("bi-caret-up-fill");
                dropdown.lastElementChild.classList.add("bi-caret-down-fill");
            }

        });
    });

    function linkAction() {
        const navToggler = document.querySelector(".navbar-toggler");
        const finMenu = document.querySelector(".fin-block-btn");
        if (!navToggler.classList.contains("collapsed")) {
            finMenu.classList.remove("show");
        }
    }

    finLinks.forEach(link => link.addEventListener("click", linkAction));

    function scrollUp() {
        const scrollUp = document.getElementById("scroll-up");
        if (this.scrollY >= 100) {
            scrollUp.classList.add("show-scroll");
        } else {
            scrollUp.classList.remove("show-scroll");
        }
    }

    window.addEventListener("scroll", scrollUp);

}

export default modalBind;