function onlyNumber(selector) {
    const onlyAll = document.querySelectorAll(selector);

    onlyAll.forEach(item => {
        item.addEventListener('input', restrictNumber);
    })
    
    function restrictNumber(e) {
        let newValue = this.value.replace(new RegExp(/[^\d.]/, 'ig'), "");
        this.value = newValue;
    }
}

export default onlyNumber;