function targetPrice() {
    const targetPricing = document.getElementById("targetPricing");

    const form = document.forms.targetPricing;

    const resTargetProfitability = document.getElementById("resTargetProfitability");
    const resTargetProductionPrice = document.getElementById("resTargetProductionPrice");
    const resTargetPrice = document.getElementById("resTargetPrice");

    function resTargetPriceText(targetPrice) {
        targetPricing.insertAdjacentHTML("beforeend",
            `<div class="alert alert-success alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>          ,       <strong>${targetPrice}</strong></p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
            <div class="alert alert-warning alert-dismissible fade show mt-3 lh-sm fs-6" role="alert">
                <p>     .</p>
                <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
            </div>
        `);
    }


    form.addEventListener("submit", (e) => {
        e.preventDefault();
        let price = (+form.targetProfitability.value * +form.targetProductionPrice.value / 100) + +form.targetProfitability.value;

        resTargetProductionPrice.textContent = +form.targetProductionPrice.value;
        resTargetProfitability.textContent = +form.targetProfitability.value;
        resTargetPrice.textContent = price;

        resTargetPriceText(price);
    });
}

export default targetPrice;